import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import FormCustomer from "./Component/FormCustomer";
import { connect } from "react-redux";
import { editcustomer, getcustomerList } from "../actions/customerAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormCustomerEdit from "./Component/FormCustomerEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "customer", {
          data: {
            kodeCustomer: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getcustomerList()));
    }
  });
}

const rowExpand = {
  renderer: (row) => (
    <div>
      <p>{`Nama Kota: ${row.kota.kodeKota}`}</p>
      <p>{`Email: ${row.info.email == null ? "-" : row.info.email} | Alamat : ${
        row.info.alamat == null ? "-" : row.info.alamat
      } | Kode Pos : ${
        row.info.kodePos == null ? "-" : row.info.kodePos
      } | Telepon : ${
        row.info.telephone == null ? "-" : row.info.telephone
      }| Handphone: ${
        row.info.handPhone == null ? "-" : row.info.handPhone
      }`}</p>
      <p>{`Kode Ext : ${row.kodeExt == null ? "-" : row.kodeExt}`}</p>
      <p>{`Nama Area : ${
        row.area.namaArea == null ? "-" : row.area.namaArea
      }`}</p>
    </div>
  ),
};

const maptoStae = (state) => {
  return {
    customerList: state.provinsi.customer,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;

class Customer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Customer: [],
      response: "",
      columns: [
        {
          dataField: "namaCustomer",
          text: "Nama Customer",
        },
        {
          dataField: "tanggalDaftar",
          text: "Tanggal Daftar",
        },
        {
          dataField: "namaToko",
          text: "Nama Toko",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeCustomer)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-target="#editModal"
                  data-toggle="modal"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editcustomer(
                        row.info.alamat,
                        row.info.email,
                        row.info.handPhone,
                        row.area.kodeArea,
                        row.kodeCustomer,
                        row.kodeExt,
                        row.kota.kodeKota,
                        row.info.kodePos,
                        row.namaCustomer,
                        row.namaToko,
                        row.statusAktif,
                        row.tanggalDaftar,
                        row.info.telephone,
                        row.kota.namaKota,
                        row.area.namaArea
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getcustomerList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getcustomerList());
  };

  handleSubmit(data) {
    var area = data.kodeArea;
    console.log(area);
    var kota = data.kodeKota;
    var areaSplit = area.split("|");
    var kotaSplit = kota.split("|");
    axios
      .post(server + "customer", {
        kota: {
          kodeKota: kotaSplit[0],
          namaKota: kotaSplit[1],
        },
        info: {
          email: data.email,
          alamat: data.alamat,
          kodePos: data.kodePos,
          contactPerson: data.contactPerson ? data.contactPerson : "-",
          telephone: data.telepon,
          fax: data.fax ? data.fax : "-",
          handPhone: data.handPhone,
        },
        namaToko: data.namaToko,
        tanggalDaftar: data.tanggalDaftar,
        statusAktif: data.status ? data.status : "AKTIF",
        namaCustomer: data.namaCustomer,
        kodeCustomer: data.kodeCustomer,
        kodeExt: data.kodeExt,
        area: {
          namaArea: areaSplit[1],
          kodeArea: areaSplit[0],
        },
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getcustomerList()))
      .then(() => this.props.dispatch(reset("Customer")));
  }

  handleEdit(data) {
    var area = data.kodeArea;
    console.log(area);
    var kota = data.kodeKota;
    var areaSplit = area.split("|");
    var kotaSplit = kota.split("|");
    axios
      .put(server + "customer", {
        kota: {
          kodeKota: kotaSplit[0],
          namaKota: kotaSplit[1],
        },
        info: {
          email: data.email,
          alamat: data.alamat,
          kodePos: data.kodePos,
          contactPerson: data.contactPerson ? data.contactPerson : "-",
          telephone: data.telepon,
          fax: data.fax ? data.fax : "-",
          handPhone: data.handPhone,
        },
        namaToko: data.namaToko,
        tanggalDaftar: data.tanggalDaftar,
        statusAktif: data.status ? data.status : "AKTIF",
        namaCustomer: data.namaCustomer,
        kodeCustomer: data.kodeCustomer,
        kodeExt: data.kodeExt,
        area: {
          namaArea: areaSplit[1],
          kodeArea: areaSplit[0],
        },
      })
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dirubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Dirubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getcustomerList()));
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h3 className="card-title mb-4" style={{ fontSize: 30 }}>
                  Data Customer
                </h3>
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data Customer
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                {this.props.customerList ? (
                  <ToolkitProvider
                    keyField="namaCustomer"
                    data={this.props.customerList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="text-left mt-5 mb-3">
                        <div className="text-right">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Pencarian"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <hr />
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          expandRow={rowExpand}
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="Not Respond"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width={1100} height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Customer
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormCustomer onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Customer
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormCustomerEdit onSubmit={(data) => this.handleEdit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoStae, null)(Customer);
