import React, { Component } from "react";
import Stepper from "react-stepper-horizontal";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import axios from "axios";
import validationCustomer from "../../Validations/validationCustomer";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" maxLength={limit} />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listKota: [],
      listArea: [],
      pilihanArea: [],
    };
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  componentDidMount() {
    axios
      .get(server + "citys")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Get Data Kota Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );

    axios
      .get(server + "areas")
      .then((res) =>
        this.setState({
          listArea: res.data,
        })
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          title: "Get Data Area Error",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }

  inputKota = (input) => (e) => {
    var result_explode = e.target.value.split("|");
    this.setState({
      kodeKota: result_explode[0],
      namaKota: result_explode[1],
    });
    var areaPilihan = this.state.listArea.filter(list => list.kodeKota === result_explode[0])
    this.setState({
      pilihanArea : areaPilihan
    })
  };
  inputArea = (input) => (e) => {
    var result_explode = e.target.value.split("|");
    this.setState({
      kodeArea: result_explode[0],
      namaArea: result_explode[1],
    });
  };

  render() {
    const { step } = this.state;
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-12 col-md-12">
              <Stepper
                steps={[
                  { title: "Data Diri" },
                  { title: "Data Lokasi" },
                  { title: "Data Nomor" },
                  // { title: "Data Rekening" },
                ]}
                activeStep={step}
              />
            </div>
            <div className={this.state.step1}>
              {/* <div className="col-md-4 col-lg-4">
                <Field
                  name="kodeCustomer"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Kode Customer"
                  limit="5"
                />
              </div> */}
              <div className="col-md-4 col-lg-4">
                <Field
                  name="kodeExt"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Kode Ext"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="namaToko"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Nama Toko"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="namaCustomer"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Nama Customer"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="tanggalDaftar"
                  type="date"
                  component={renderField}
                  label="Tanggal Daftar"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <label htmlFor="">STATUS</label>
                <Field
                  name="status"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  component="select"
                  label="Nama Customer"
                >
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                  <option value="AKTIF" key="AKTIF">
                    AKTIF
                  </option>
                  <option value="NON-AKTIF" key="NON-AKTIF">
                    NON-AKTIF
                  </option>
                </Field>
              </div>
              <div className="col-lg-12 col-md-12">
                <div className="text-right">
                  <button
                    id="next_1"
                    name="next_1"
                    className="btn btn-primary"
                    type="button"
                    onClick={() => this.nextStep()}
                  >
                    Next
                  </button>
                </div>
              </div>
            </div>
            <div className={this.state.step2}>
              <div className="col-md-4 col-lg-4">
                <label htmlFor="">Kota</label>
                <Field
                  name="kodeKota"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  component="select"
                  label="Nama Customer"
                  onChange={this.inputKota()}
                >
                  <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                  {this.state.listKota.map((kota) => (
                    <option
                      value={kota.kodeKota + "|" + kota.namaKota}
                      key={kota.kodeKota}
                    >
                      {kota.namaKota}
                    </option>
                  ))}
                </Field>
              </div>
              <div className="col-md-4 col-lg-4">
                <label htmlFor="">Area</label>
                <Field
                  name="kodeArea"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  className="form-control"
                  component="select"
                  label="Nama Customer"
                  onChange={this.inputArea()}
                >
                  <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                  {this.state.pilihanArea.map((area) => (
                    <option
                      value={area.kodeArea + "|" + area.namaArea}
                      key={area.kodeArea}
                    >
                      {area.namaArea}
                    </option>
                  ))}
                </Field>
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="kodePos"
                  type="number"
                  component={renderField}
                  label="Kode Pos"
                />
              </div>
              <div className="col-md-6 col-lg-6">
                <Field
                  name="alamat"
                  type="text"
                  normalize={(value) => value && value.toUpperCase()}
                  component={renderField}
                  label="Alamat"
                />
              </div>
              <div className="col-lg-12 col-md-12">
                <div className="row">
                  <div className="text-left col-md-6">
                    <button
                      id="back_1"
                      name="back_1"
                      className="btn btn-primary"
                      type="button"
                      onClick={() => this.prevStep()}
                    >
                      Back
                    </button>
                  </div>
                  <div className="text-right col-md-6">
                    <button
                      id="next_2"
                      name="next_2"
                      className="btn btn-primary"
                      type="button"
                      onClick={() => this.nextStep()}
                    >
                      Next
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div className={this.state.step3}>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="email"
                  type="email"
                  component={renderField}
                  label="Email"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="telepon"
                  type="number"
                  component={renderField}
                  label="Nomor Telepon"
                />
              </div>
              <div className="col-md-4 col-lg-4">
                <Field
                  name="handPhone"
                  type="number"
                  component={renderField}
                  label="Handphone"
                />
              </div>
              <div className="col-lg-12 col-md-12">
                <div className="row">
                  <div className="text-left col-md-6">
                    <button
                      id="back_2"
                      name="back_2"
                      className="btn btn-primary"
                      type="button"
                      onClick={() => this.prevStep()}
                    >
                      Back
                    </button>
                  </div>
                  <div className="text-right col-md-6">
                    <button
                      type="submit"
                      className="btn btn-primary"
                      onClick={() =>
                        this.setState({
                          step: 0,
                          step1: "row",
                          step2: "row d-none",
                          step3: "row d-none",
                        })
                      }
                    >
                      Simpan
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
    // case 3:
    //   return (
    //     <div className="col-lg-12 col-md-12">
    //       <Stepper
    //         steps={[
    //           { title: "Data Diri" },
    //           { title: "Data Lokasi" },
    //           { title: "Data Nomor" },
    //           { title: "Data Rekening" },
    //         ]}
    //         activeStep={step}
    //       />
    //       <div className="mt-5">
    //         <FormCustomer_Step4
    //           nextStep={this.nextStep}
    //           prevStep={this.prevStep}
    //           inputChange={this.inputChange}
    //           submit={this.submit}
    //           values={values}
    //         />
    //       </div>
    //     </div>
    //   );
  }
}

FormCustomer = reduxForm({
  form: "Customer",
  validate: validationCustomer,
  enableReinitialize: true,
})(FormCustomer);
export default connect()(FormCustomer);
